.container {
  max-width: 1160px;
  margin: auto; }

body {
  background-color: #313131;
  background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234a4356' fill-opacity='0.22'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E"); }

.site-header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  position: relative;
  background-color: #2C3E50;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }

.logotype {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #EEEEEE; }

.site-navigation {
  display: flex;
  align-items: center; }
  .site-navigation ul li {
    display: inline-flex;
    margin-right: 20px; }
    .site-navigation ul li:hover a {
      color: #2574A9; }
    .site-navigation ul li a {
      text-decoration: none;
      color: #D2D7D3;
      font-weight: 700; }

.profile {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #DEDBD8;
  position: relative; }
  .profile::before {
    left: 0;
    width: 1px;
    height: 100%;
    background-color: #33485d;
    margin-right: 14px;
    content: ""; }
  .profile::after {
    right: 0;
    width: 1px;
    height: 100%;
    background-color: #33485d;
    margin-left: 14px;
    content: ""; }

.profile-avatar {
  width: 24px;
  height: 24px;
  border-radius: 36em;
  display: inline-flex; }

.profile-info-block {
  display: inline-flex;
  flex-direction: column;
  text-align: left;
  padding-top: 9px;
  margin-left: 6px; }

.profile-nickname {
  line-height: 11px; }

.profile-balance {
  font-weight: 700; }
  .profile-balance::after {
    content: 'BTC';
    font-size: 12px;
    padding-left: 5px; }
